<template>
    <div id="confirmPage">
        <div id="orderInfoCSS">

            <h1>Confirmation number</h1>
            <p class="orderInfoCSS">{{ $store.state.orderDetail.order.confirmationNumber }}</p>
        <p class="orderDateCSS">Transaction Date: {{ $store.state.orderDetail.order.dateCreated.month }} {{ $store.state.orderDetail.order.dateCreated.dayOfMonth }},
            {{ $store.state.orderDetail.order.dateCreated.year }} {{ $store.state.orderDetail.order.dateCreated.hour}}:{{$store.state.orderDetail.order.dateCreated.minute}}</p>
        </div>
        <div id="customerInfo">
            <h1>Customer Information</h1>
            <p>{{ $store.state.orderDetail.customer.customerName }}</p>
            <p>{{ $store.state.orderDetail.customer.address }}</p>
            <h1>Customer Contact Information</h1>
            <p>{{ $store.state.orderDetail.customer.email}}</p>
            <p>{{ $store.state.orderDetail.customer.phone }}</p>
            <h1>Payment Information</h1>
            <p class="ccInfoCSS">{{getCcInfo($store.state.orderDetail.customer)}}</p>
        </div>
        <table>
            <tr
                    v-for="(book,index) in this.$store.state.orderDetail.books"
                    :key="book.bookId"
            >
                <td class="bookQty">{{ $store.state.orderDetail.lineItems[index].quantity }}</td>
                <td class="bookTitleCSS">{{ book.title }}</td>
                <td class="bookPriceCSS cellPrice"> {{bookTotal(book.price, $store.state.orderDetail.lineItems[index].quantity) | currency}}</td>

            </tr>
            <tr>
                <td>-</td>
                <td class="cellPrice">Surcharge</td>
                <td class="cellPrice">{{$store.state.surcharge | currency}}</td>
            </tr>
            <tr>
                <td>-</td>
                <td class="cellPrice totals">Total</td>
                <td class="cellPrice">{{$store.state.orderDetail.order.amount | currency}}</td>
            </tr>

        </table>


    </div>
</template>

<script>
    export default {
        name: "Confirmation",
        components: {

        },
        methods: {
            bookTotal: function(bookPrice, bookQty) {
                return bookPrice * bookQty;
            },
            getCcInfo: function(customer) {
                let lastFour = customer.ccNumber.substr(customer.ccNumber.length - 4, customer.ccNumber.length);
                return "Credit card ending in " + lastFour + ". Expires " + customer.ccExpDate.monthValue +
                    "/" + customer.ccExpDate.year;
            }
        }
    }
</script>

<style scoped>
    #confirmPage{
        background-color: #5b9bd5;
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;

    }
    #orderInfoCSS {
        margin-top: 10px;
        margin-left: 10px;
        width: 350px;
        border: 1px solid black;
        border-radius: 5px;
        background-color: #9dc3e6;
    }
    #customerInfo {
        margin-bottom: 10px;
        margin-top: 10px;
        margin-left: 10px;
        width: 350px;
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
        border: 1px solid black;
        border-radius: 5px;
        background-color: #FFF2CC;
    }
    table{
        width: 500px;
        margin-left: 10px;
        margin-bottom: 10px;
        margin-bottom: 10px;
    }
    h1 {
        margin-top: 5px;
        margin-left: 5px;
        font-weight: bold;
    }
    p {
        margin-top: 1px;
        margin-left: 5px;
    }
    td {
        border: 1px solid black;
        text-align: left;
        padding: 8px;
        font-size: 25px;
        font-family: "Calibri", sans-serif;
        font-weight: lighter;
    }
    .cellPrice {
        text-align: right;
    }

    .totals {
        font-weight: bold;
    }

    tr:nth-child(even) {
        background-color: #fff2cc;
    }

    tr:nth-child(odd) {
        background-color: #9dc3e6;
    }

    .orderInfoCSS {
        margin-left: 10px;
    }

    .ccInfoCSS {
        margin-bottom: 10px;
    }

    .orderDateCSS {
        margin-left: 10px;
        margin-bottom: 10px;
    }
</style>